* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    outline: none;
    list-style: none;
    word-wrap: break-word;
  }
  
  body {
    color: #fff;
    background: #161617;
    line-height: 1.3;
    font-weight: 400;
    font-size: 14px;
    font-family: 'Rubik', sans-serif;
    overflow-x: hidden !important;
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Josefin Sans', sans-serif;
  }
  
  p {
    font-size: 15px;
    color: #8f8f8f;
    line-height: 1.8;
    margin: 0;
  }
  
  img {
    width: 100%;
    height: auto;
  }
  
  span, a, a:hover {
    display: inline-block;
    text-decoration: none;
    color: inherit;
  }
  
  .section-padding {
    padding: 120px 0;
  }
  
  .section-head {
    margin-bottom: 80px;
    position: relative;
  }
  .section-head h4 {
    font-size: 70px;
    font-weight: 800;
    font-family: inherit;
    letter-spacing: 2px;
    color: #222;
    -webkit-transition: all .4s;
    transition: all .4s;
  }
  
  section:hover .section-head h4 {
    color: transparent;
    -webkit-text-stroke: 1px #333;
  }
  
  .wave {
    position: absolute;
    width: 100%;
    left: 0;
    top: -1px;
    fill: #161617;
  }
  .wave.dark {
    fill: #0F0F0F;
  }
  
  .bg-dark {
    background: #0F0F0F !important;
  }
  
  .o-hidden {
    overflow: hidden;
  }
  
  .position-re {
    position: relative;
  }
  
  .full-width {
    width: 100% !important;
  }
  
  .lg-line-height {
    line-height: 1.5;
  }
  
  .bg-img {
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  .bg-fixed {
    background-attachment: fixed;
  }
  
  .valign {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  
  .v-middle {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    -webkit-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
  }
  
  .cd-headline {
    line-height: 1.2;
  }
  
  .cd-words-wrapper {
    display: inline-block;
    position: relative;
    text-align: center;
  }
  
  .cd-words-wrapper b {
    font-weight: inherit;
    color: #c49b66;
    display: inline-block;
    position: absolute;
    white-space: nowrap;
    left: 0;
    top: 0;
  }
  
  .cd-words-wrapper b.is-visible {
    position: relative;
  }
  
  .no-js .cd-words-wrapper b {
    opacity: 0;
  }
  
  .no-js .cd-words-wrapper b.is-visible {
    opacity: 1;
  }
  
  .cd-headline.push b {
    opacity: 0;
  }
  
  .cd-headline.push b.is-visible {
    opacity: 1;
    -webkit-animation: push-in 0.6s;
    animation: push-in 0.6s;
  }
  
  .cd-headline.push b.is-hidden {
    -webkit-animation: push-out 0.6s;
    animation: push-out 0.6s;
  }
  
  @-webkit-keyframes push-in {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-100%);
    }
    60% {
      opacity: 1;
      -webkit-transform: translateX(10%);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateX(0);
    }
  }
  @keyframes push-in {
    0% {
      opacity: 0;
      -webkit-transform: translateX(-100%);
      transform: translateX(-100%);
    }
    60% {
      opacity: 1;
      -webkit-transform: translateX(10%);
      transform: translateX(10%);
    }
    100% {
      opacity: 1;
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
  }
  @-webkit-keyframes push-out {
    0% {
      opacity: 1;
      -webkit-transform: translateX(0);
    }
    60% {
      opacity: 0;
      -webkit-transform: translateX(110%);
    }
    100% {
      opacity: 0;
      -webkit-transform: translateX(100%);
    }
  }
  @keyframes push-out {
    0% {
      opacity: 1;
      -webkit-transform: translateX(0);
      transform: translateX(0);
    }
    60% {
      opacity: 0;
      -webkit-transform: translateX(110%);
      transform: translateX(110%);
    }
    100% {
      opacity: 0;
      -webkit-transform: translateX(100%);
      transform: translateX(100%);
    }
  }
