  .butn {
    padding: 12px 34px;
    background: #fff;
    border-radius: 30px;
    border: 1px solid transparent;
    position: relative;
    z-index: 3;
    margin-left: 10px;
    margin-right: 10px;
    -webkit-box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
    -webkit-transition: all .4s;
    transition: all .4s;
    cursor: pointer;
    outline: none !important;
    overflow: hidden;
  }
  .butn span {
    position: relative;
    z-index: 2;
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
  }
  .butn:before, .butn:after {
    content: '';
    width: 0;
    height: 100%;
    background: #fff;
    position: absolute;
    left: 0;
    top: 0;
    -webkit-transition: width 0.4s;
    transition: width 0.4s;
    z-index: 1;
    opacity: .4;
  }
  .butn:after {
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
    background: #fff;
    opacity: 1;
  }
  .butn:hover:before, .butn:hover:after {
    width: 100%;
  }
  .butn:hover:after {
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
  }
  .butn:hover span {
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
  }
  
  .butn-bg {
    background: #c49b66;
    border-color: #c49b66;
    color: #fff;
  }
  .butn-bg:hover span {
    color: #c49b66;
  }
  
  .butn-light {
    background: #fff;
  }
  .butn-light:before, .butn-light:after {
    background: #c49b66;
  }
  .butn-light:hover span {
    color: #fff;
  }
  .butn-light span {
    color: #c49b66;
  }
  
  .butn-bord {
    background: transparent;
    border: 1px solid #eee;
  }
  .butn-bord:before, .butn-bord:after {
    background: #c49b66;
  }
  .butn-bord:hover {
    border-color: #c49b66;
  }
  .butn-bord:hover span {
    color: #fff;
  }
  
